export default {
  name: "user.dialog",
  props: {
    show: undefined,
    text: undefined
  },
  data() {
    return {
      submit_loading: false,
      user: {},
      referrer: ''
    }
  },
  created() {
    if (!this.text) {
      this.text = this.$l('user.login_tip');
    }
    window._user_dialog = this;
    if (document.referrer) {
      this.referrer = document.referrer;
    }
    console.log('referrer:',this.referrer);
  },
  methods: {
    on_register: function () {
      this.show = false;
      this.go('/user/register');
    },
    on_login: function () {
      this.show = false;
      this.go('/user/login');
    },

    on_submit: function () {

      if (!this.user.username || this.user.username.length < 4) {
        return this.$message('用户名过短,不少于4位', {
          theme: "error",
          position: "top-left",
          duration: 5000
        });
      }

      if (!this.user.password || this.user.password.length <= 6) {
        return this.$message('密码过短，不能少于6位', {
          theme: "error",
          position: "top-left",
          duration: 5000
        });
      }

      this.submit_loading = true;
      var post_data = this.user
      post_data.referrer = this.referrer;

      this.$axios.post('/app/user/login', post_data).then((response) => {
        this.loading = false;
        var response_data = response.data;
        console.log("response_data:", response_data)

        var userinfo = response_data.user;
        var token = response_data.token;
        this.$message('登录成功，正在跳转', {
          theme: "normal",
          position: "top-left",
          duration: 5000
        });

        this.$store.commit('login', {
          'userinfo': userinfo,
          'token': token
        });
        if (!this.referrer) {
          this.referrer = '/'
        }
        console.log('this.referrer:', this.referrer);
        this.$router.push(this.referrer);
      }).catch((error) => {
        console.log('error:', error);
        this.submit_loading = false;

      });
      return false;
    }
  },
  watch: {
    show: function (newValue, oldValue) {
      console.log('call watch show change');
      this.$emit('update:show', newValue);
    },
    $route(to, from) {
      this.show = false;
    }
  },

}
